.monaco-tree {
  height: 100%;
  width: 100%;
  white-space: nowrap;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: -moz-none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  position: relative;
}

.monaco-tree > .monaco-scrollable-element {
  height: 100%;
}

.monaco-tree > .monaco-scrollable-element > .monaco-tree-wrapper {
  height: 100%;
  width: 100%;
  position: relative;
}

.monaco-tree .monaco-tree-rows {
  position: absolute;
  width: 100%;
  height: 100%;
}

.monaco-tree .monaco-tree-rows > .monaco-tree-row {
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  cursor: pointer;
  overflow: hidden;
  width: 100%;
  touch-action: none;
}

.monaco-tree .monaco-tree-rows > .monaco-tree-row > .content {
  position: relative;
  height: 100%;
}

.monaco-tree-drag-image {
  display: inline-block;
  padding: 1px 7px;
  border-radius: 10px;
  font-size: 12px;
  position: absolute;
}

/* for OS X ballistic scrolling */
.monaco-tree .monaco-tree-rows > .monaco-tree-row.scrolling {
  display: none;
}

/* Expansion */
.monaco-tree .monaco-tree-rows.show-twisties > .monaco-tree-row.has-children > .content:before {
  content: " ";
  position: absolute;
  display: block;
